* {
  margin: 0;
  padding: 0;
  font-family: "Avenir Next";
}

html {
  font-size: 16px;
}

h1 {
  font-size: 2rem;
  font-weight: 600;
}

h2 {
  font-size: 1.75rem;
  font-weight: 600;
}

h3 {
  font-size: 1.5rem;
  font-weight: 700;
}

h4 {
  font-size: 1.375rem;
  font-weight: 700;
}

h5 {
  font-size: 1.25rem;
  font-weight: 500;
}

p {
  font-weight: 500;
  font-size: 1.125rem;
  color: #1b1e1f;
}

.button {
  position: relative;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

input:focus {
  outline: none;
}

input[type="submit"] {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  font-size: 0.875rem;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
}

.button:hover {
  opacity: 0.9;
}

.button:active {
  top: 1px;
  opacity: 1;
}
